.chat-message {
  max-width: 85%;
  padding: 10px 15px;
  margin: 8px 0;
  border-radius: 12px;
  font-family: sans-serif;
  background-color: #f8f8f8;

  /* ✅ Ensure all long content wraps properly */
  white-space: pre-wrap;        /* Respect line breaks and wrap */
  overflow-wrap: anywhere;      /* ✅ Breaks long unbreakable words like URLs */
  word-break: break-word;       /* Break words when necessary */
  box-sizing: border-box;

  backdrop-filter: blur(8px);
  
}

.user-message {
  background-color: #e0cffe;    /* Light purple */
  color: #333;
  align-self: flex-end;
  text-align: right;
}

.server-message {
  background-color: #f0e6ff;    /* Slightly lighter purple */
  color: #333;
  align-self: flex-start;
  text-align: left;
}

#text-input{
    border:none;
    border-radius:8px;
    grid-area:"b";
    transform:scale(1.5);
}
#send{
    grid-area:"send";
    transform:scale(1.5);
    backdrop-filter: blur(8px);
    border:none;
    border-radius:8px;
    width:20%;
}
#mic{
    grid-area:"mic";
    transform:scale(1.5);
    backdrop-filter: blur(8px);
    border:none;
    border-radius:8px;
    width:20%;
}

.input-box{
    flex-direction: column;
    display:flex;

}
.buttons{
    width:100%;
    display:flex;
    justify-content: space-around;
    margin-top: 30px;
}

